import React from 'react';
import PostService from '../services/PostService';
import { Link } from 'react-router-dom';

const Profile: React.FC = () => {

    const downloadProfile = () => {
        const element = document.createElement('a');
        const data = JSON.stringify({login: PostService.getInfo().login, api_key: PostService.getInfo().apikey, tags: PostService.getTags()});
        const file = new Blob([data], {type: 'application/json'});
        element.href = URL.createObjectURL(file);
        element.download = 'e1547e-profile.e8e';
        document.body.appendChild(element);
        element.click()
    }

    return(
        <>
            <p><Link to="/e1547e">{"<< Return"}</Link></p>
            <p>Login: {PostService.getInfo().login}</p>
            <p>API_key: {PostService.getInfo().apikey}</p>
            <p>Tagamount: {PostService.getTags().length}</p>
            <button onClick={downloadProfile}>Save profile</button>
        </>
    )
}

export default Profile;