import React, { useState, useEffect } from 'react';
import PostService from '../../services/PostService';
import useInterval from 'use-interval'
import { Link } from 'react-router-dom';

const TagList: React.FC = () => {
    const [tags, setTags] = useState<string[]>([]);
    const [isOpen, setIsOpen] = useState<boolean>(true);

    const getTags = () => {
        setTags(PostService.getTags());
    }

    useEffect(() => {
        getTags();
    }, []);

    useInterval(() => {
        getTags();
    }, 1000);

    const removeTag = async (tag:string) => {
        console.log('removing tag ' + tag)
        PostService.removeTag(tag);
        getTags()
    }

    const downloadTags = () => {
        const element = document.createElement('a');
        const file = new Blob([PostService.getTags().join('\n')], {type: 'text/plain'});
        element.href = URL.createObjectURL(file);
        element.download = 'tags.txt';
        document.body.appendChild(element);
        element.click()
    }

    const toggleOpen = (event:any) => {
        event.preventDefault();
        setIsOpen(isOpen === false)
    }

    return (
        <>
            <table>
                <thead>
                    <tr>
                        <th>Tag</th>
                        <th><a href='#' onClick={toggleOpen} className='highlight-link'>{isOpen ? '' : ''}</a></th>
                    </tr>
                </thead>
                <tbody style={{display: isOpen ? '' : 'none'}}>
                    {tags && 
                        tags.map((tag, index) => (
                            <tr key={index}>
                                <td><a className='tag-list-item' id={tag} href={`https://e621.net/posts?tags=${tag}`} target='_blank'>{`[${tag}]`}</a></td>
                                <td><a className='tag-list-item' href='#' onClick={(event) => {event.preventDefault(); if(window.confirm(`Are you sure you want "${tag}" removed?`)) removeTag(tag)}}>X</a></td>
                            </tr>
                        ))}
                </tbody>
            </table>
            <button onClick={downloadTags}>Download Tags</button>
            <p><Link to='/e1547e/add-multiple-tags'>Add multiple tags</Link></p>
        </>
    )
}

export default TagList