import { AxiosResponse } from 'axios';
import React, { useState, useEffect } from 'react';
import PostService from '../../services/PostService';

const LoginView: React.FC = () => {
    const [loginInput, setLoginInput] = useState<string>('');
    const [apiKeyInput, setApiKeyInput] = useState<string>('');
    const [statusMessage, setStatusMessage] = useState<string>('');

    const handleSubmit = async (event:any) => {
        event.preventDefault();
        if (!(PostService.register(loginInput, apiKeyInput))){
            setStatusMessage('Invalid login credentials');
        } else {
            const loggedin = PostService.loggedin();
            console.log(loggedin);
            if(loggedin.all){
                window.location.reload();
            }
        }
    }

    const handleFile = (e:any) => {
        const fileReader = new FileReader();
        fileReader.readAsText(e.target.files[0], "UTF-8");
        fileReader.onload = e => {
            const data:{login:string, api_key:string, tags:string[]} = JSON.parse(String(e.target?.result));
            PostService.setProfile(data);
            window.location.reload();
        }
    }

    return (
        <>
            <form onSubmit={handleSubmit}>
                <h3>Sign In</h3>
                {statusMessage !== '' &&
                    <p>{statusMessage}</p>}
                <div className="mb-3">
                    <label>Username</label>
                    <input
                        type="text"
                        placeholder="login"
                        value={loginInput}
                        onChange={(event) => setLoginInput(event.target.value)}
                    />
                </div>
                <div className="mb-3">
                    <label>Api_key</label>
                    <input
                        type="text"
                        placeholder="api_key"
                        value={apiKeyInput}
                        onChange={(event) => setApiKeyInput(event.target.value)}
                    />
                </div>
                <div className="d-grid">
                    <button type="submit">
                        Submit
                    </button>
                </div>
            </form>
            <div>
                <p>Upload profile:</p>
                <input type="file" onChange={handleFile} />
            </div>
        </>
    )
}

export default LoginView