import { AxiosResponse } from 'axios';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import PostService from '../../services/PostService';

type Props = {
    refreshPosts:any;
}

const AddTags: React.FC<Props> = ({ refreshPosts }:Props) => {
    const [tagInput, setTagInput] = useState<string>('');
    const [statusMessage, setStatusMessage] = useState<string>('');

    const handleSubmit = async (event:any) => {
        event.preventDefault();
        if (tagInput.trim() === '') {
            setStatusMessage('No tag given')
        } else {
            PostService.addTag(tagInput);
            setStatusMessage('Tag added');
            setTagInput('');
            refreshPosts();
        }
    }

    const removeAllTags = () => {
        PostService.removeAllTags();
        setStatusMessage('All tags removed');
        refreshPosts();
    }

    return(
        <>
            <p>{statusMessage}</p>
            <form onSubmit={handleSubmit}>
                <label>
                    Tag:
                    <input type='text' value={tagInput} onChange={(event) => setTagInput(event.target.value)}/>
                </label>
                <input type='submit' value='Submit'/>
            </form>
            <p><a href='#' onClick={() => {if(window.confirm('Are you sure you want to remove all tags?')) removeAllTags()}}>Remove all tags</a></p>
        </>
    )
}

const AddMultipleTags: React.FC = () => {
    const [tagInput, setTagInput] = useState<string>('');
    const [statusMessage, setStatusMessage] = useState<string>('');

    const handleSubmit = async (event:any) => {
        event.preventDefault();
        if (tagInput.trim() === '') {
            setStatusMessage('No tag given')
        } else {
            let tags = tagInput.split('\n');
            for(let tag of tags){
                PostService.addTag(tag);
            }
            setStatusMessage(`${tags.length} tags added.`);
            setTagInput('');
        }
    }

    return (
        <>
            <p><Link to='/e1547e'>&lt;&lt; Return</Link></p>
            <p>{statusMessage}</p>
            <form onSubmit={handleSubmit}>
                <label>
                    Tags:
                    <textarea value={tagInput} onChange={(event) => setTagInput(event.target.value)}/>
                </label>
                <input type='submit' value='Submit'/>
            </form>
        </>
    )
}
export default { AddTags, AddMultipleTags };