import axios from "axios";
import { Post, Posts } from "../types";
import Cookies from "universal-cookie";

let cookies = new Cookies();

const devMode = false;

const getPostsNormal = (page:number, tags:string[]) => {
    let startDate = new Date();
    startDate.setDate(startDate.getDate()-((page*3)+2));
    let endDate = new Date();
    endDate.setDate(endDate.getDate()-(page*3)+1);
    let datestring = `${startDate.getFullYear()}-${startDate.getMonth()+1}-${startDate.getDate()}..${endDate.getFullYear()}-${endDate.getMonth()+1}-${endDate.getDate()}`
    let url = `https://e621.net/posts.json?limit=320&login=${cookies.get('login')}&api_key=${cookies.get('api_key')}&tags=`
    tags.forEach(tag => {
        url = `${url}~${tag}+`;
    });
    url = url + `date:${datestring}`
    devMode && console.log('Sending request to ' + url)
    return axios.get<Posts>(url);
}

const getPostsComplex = (page:number, tags:string[]) => {
    let startDate = new Date();
    startDate.setDate(startDate.getDate()-((page*3)+2));
    let endDate = new Date();
    endDate.setDate(endDate.getDate()-(page*3)+1);
    let datestring = `${startDate.getFullYear()}-${startDate.getMonth()+1}-${startDate.getDate()}..${endDate.getFullYear()}-${endDate.getMonth()+1}-${endDate.getDate()}`
    let url = `https://e621.net/posts.json?limit=320&login=${cookies.get('login')}&api_key=${cookies.get('api_key')}&tags=`;
    tags.forEach(tag => {
        url = `${url}${tag}+`;
    });
    url = url + `date:${datestring}`
    devMode && console.log('Sending request to ' + url)
    return axios.get<Posts>(url);
}

const addTag = (tag:string) => {
    let tags:string[] = getTags();
    if (tag.includes('order:') === false && tag.includes('date:') === false && tags.indexOf(tag) === -1) {
        tags.push(tag.trim());
    }
    cookies.set('tags', tags.toString());
};

const removeAllTags = () => {
    cookies.remove('tags');
}

const removeTag = (tag:string) => {
    let tags = getTags();
    const splice = tags.splice(tags.indexOf(tag), 1);
    devMode && console.log(splice);
    cookies.set('tags', tags.toString());
    if (tags.length === 0) {
        cookies.remove('tags')
    }
}

const getTags = ():string[] => {
    let tagstring:string|null = cookies.get('tags');
    if (tagstring == null){
        return [];
    } else {
        return tagstring.split(',').sort();
    }
}

const loggedin = ():{login:boolean, api_key:boolean, all:boolean} => {
    const login = cookies.get('login') != null;
    const api_key = cookies.get('api_key') != null;
    return {login: login, api_key: api_key, all: login && api_key}
}

const register = (login:string, api_key:string):boolean => {
    if (login.trim() !== ''){
        cookies.set('login', login);
    }
    if (api_key.trim() !== ''){
        cookies.set('api_key', api_key);
    }
    return true;
}

const setImagesPerRow = (amount:number) => {
    cookies.set('images-per-row', amount);
}

const getImagesPerRow = ():number => {
    let amount = cookies.get('images-per-row');
    return amount != null ? Number(amount) : 4;
}

const getPool = async (poolid:number) => {
    const api_key = cookies.get('api_key');
    const login = cookies.get('login');
    const pool = (await axios.get<{name:string, post_ids:number[]}>(`https://e621.net/pools/${poolid}.json?login=${login}&api_key=${api_key}`)).data;
    let posts:Post[] = [];
    if (pool.post_ids.length > 100){
        for (let i = 0 ; i < pool.post_ids.length ; i = i + 100){
            let postslice = pool.post_ids.slice(i, i + 100);
            const res = await axios.get<Posts>(`https://e621.net/posts.json?limit=320&login=${login}&api_key=${api_key}&tags=id:${postslice.toString()}`);
            posts = [...posts, ...res.data.posts];
        }
    } else {
        posts = (await axios.get<Posts>(`https://e621.net/posts.json?limit=320&login=${login}&api_key=${api_key}&tags=id:${pool.post_ids.toString()}`)).data.posts;
    }
    let resultposts:Post[] = [];
    for (let postid of pool.post_ids) {
        for (let i of posts) {
            if (i.id === postid){
                resultposts.push(i);
                break;
            }
        }
    }
    return resultposts;
}

const getAllTagsFromPost = (post:Post):string[] => {
    return [...post.tags.artist, ...post.tags.general, ...post.tags.species, ...post.tags.character, ...post.tags.copyright, ...post.tags.lore, ...post.tags.meta]
}

const getInfo = () => {
    return {login: cookies.get('login'), apikey: cookies.get('api_key')}
}

const setProfile = (data:{login:string, api_key:string, tags:string[]}) => {
    cookies.set("login", data.login);
    cookies.set("api_key", data.api_key);
    cookies.set("tags", data.tags.toString());
}


const PostService = {
    getPostsNormal,
    getPostsComplex,
    addTag,
    removeAllTags,
    getTags,
    removeTag,
    loggedin,
    register,
    getImagesPerRow,
    setImagesPerRow,
    getPool,
    getAllTagsFromPost,
    getInfo,
    setProfile
}

export default PostService