import { AxiosResponse } from 'axios';
import React, { useState, useEffect } from 'react';
import PostService from '../../services/PostService';
import { Post, Posts } from '../../types';
import InfiniteScroll from 'react-infinite-scroller';
import TagList from '../tag-list';
import addTags from '../add-tags';
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";
import Modal from 'react-modal'
import ReactPlayer from 'react-player';
import { Button } from '@mui/material';
import { Link } from 'react-router-dom';

const devMode = true;

const PostOverview: React.FC = () => {
    const [posts, setPosts] = useState<Post[]>([]);
    const [page, setPage] = useState<number>(0);
    const [isLoading, setLoadingState] = useState<boolean>(false);
    const [loadMessage, setLoadMessage] = useState<string>('Load more ...');
    const [hoveredPost, setHoveredPost] = useState<number>(0);
    const [imageViewerIsOpened, setImageViewerIsOpened] = useState<boolean>(false);
    const [videoIsOpen, setVideoIsOpen] = useState<boolean>(false);
    const [viewedImage, setViewedImage] = useState<Post|undefined>(undefined);
    const [imagesPerRow, setImagesPerRow] = useState<number>(3);
    const [showAllArtists, setShowAllArtists] = useState<boolean>(true);
    const [extendedPost, setExtendedPost] = useState<number>(0);
    const [isPoolOpened, setPoolOpened] = useState<boolean>(false);
    const [poolPosts, setPoolPosts] = useState<Post[]>([]);
    const [prevAndNextImage, setPrevAndNextImage] = useState<{prev:Post|undefined, next:Post|undefined}>({prev: undefined, next: undefined});
    const [isOpenedImageAPool, setIsImageAPool] = useState<boolean>(false);
    const [poolImageSize, setPoolImageSize] = useState<number>(100);
    const [screenWidth, setScreenWidth] = useState<number>(1000);
    const [nextPosts, setNextPosts] = useState<Post[]>([]);

    useEffect(() => {
        devMode && console.log('I fire once')
        setImagesPerRow(PostService.getImagesPerRow());
        window.addEventListener('resize', () => setScreenWidth(window.innerWidth))
    }, []);

    useEffect(() => {
        document.body.style.overflow = imageViewerIsOpened || isPoolOpened || videoIsOpen ? 'hidden' : 'unset';
    }, [imageViewerIsOpened, isPoolOpened, videoIsOpen])

    const loadMessageFormatter = (current:number, total:number, isComplex:boolean) => {
        let string = `Sending ${isComplex ? 'complex' : 'normal'} request ${current}/${total}`;
        setLoadMessage(string);
    }

    const getPosts = async () => {
        if(!isLoading){
            setLoadingState(true)
            let local_nextPosts = nextPosts;
            const post_amount = 20;
            if (local_nextPosts.length < post_amount){
                const incomingPosts = await getNextPosts();
                local_nextPosts = [...local_nextPosts, ...incomingPosts];
            }
            const postSection = local_nextPosts.slice(0, post_amount);
            local_nextPosts.splice(0, post_amount);
            setNextPosts(local_nextPosts);
            setPosts([...posts, ...postSection]);

            setTimeout(() => setLoadingState(false), 3000);
        }
    }

    const getNextPosts = async () => {
        devMode && console.log("Loading posts ...")
        let normaltags:string[] = [];
        let complextags:string[] = [];

        PostService.getTags().forEach(tag => {
            if (tag.includes(' ') || tag.includes(':')) {
                complextags.push(tag);
            } else {
                normaltags.push(tag);
            }
        })

        let tempposts:Post[] = []
        let teller = 1;
        for(let i = 0 ; i < complextags.length ; i++){
            loadMessageFormatter(teller++, complextags.length + Math.floor(normaltags.length/39)+1, true)
            let tag = complextags[i];
            const res: AxiosResponse<Posts> = await PostService.getPostsComplex(page, tag.split(' '));
            devMode && console.log(`Amount of posts (max 320) ${res.data.posts.length}`)
            tempposts = [...tempposts, ...res.data.posts]
            devMode && console.log("complex finished")
        }
        devMode && console.log('complex loop finished')

        if (normaltags.length !== 0){
            if(normaltags.length >= 39){
                for(let i = 0 ; i < normaltags.length ; i = i + 39){
                    loadMessageFormatter(teller++, complextags.length + Math.floor(normaltags.length/39)+1, false);
                    let tagslice = normaltags.slice(i, i + 39);
                    const res: AxiosResponse<Posts> = await PostService.getPostsNormal(page, tagslice);
                    devMode && console.log(`Amount of posts (max 320) ${res.data.posts.length}`)
                    tempposts = [...tempposts, ...res.data.posts]
                }
            } else {
                loadMessageFormatter(teller, complextags.length, false);
                const res: AxiosResponse<Posts> = await PostService.getPostsNormal(page, normaltags);
                devMode && console.log(`Amount of posts (max 320) ${res.data.posts.length}`)
                tempposts = [...tempposts, ...res.data.posts]
            }
        }

        let uniquePosts:Post[] = [];
        const arrayContainsId = (array:Post[], id:number) => {
            for(let i of array){
                if(i.id === id){
                    return true;
                }
            }
            return false;
        }
        for(let i of tempposts){
            if(arrayContainsId(uniquePosts, i.id) === false){
                uniquePosts.push(i);
            }
        }

        devMode && console.log('sorting ...')
        uniquePosts.sort((a:Post, b:Post) => {
            let left = new Date(a.created_at);
            let right = new Date(b.created_at);
            if(left > right) {
                return -1;
            }
            if (right > left) {
                return 1;
            }
            return 0;
        })
        setLoadMessage('Load more ...')
        setPage(page + 1)
        return uniquePosts;
    };

    const getMore = (event:any) => {
        event.preventDefault();
        getPosts()
    }

    const refreshPosts = () => {
        devMode && console.log('Refreshing posts')
        window.location.reload()
    }

    const changeImagesPerRow = (amount:number) => {
        let newAmount = imagesPerRow + amount;
        if (newAmount > 0 && newAmount <= 10) {
            setImagesPerRow(newAmount);
            PostService.setImagesPerRow(newAmount);
        }
    }

    const imageViewer = (post:Post, isPool:boolean) => {
        setIsImageAPool(isPool);
        setViewedImage(post);
        if (post.file.ext === 'webm'){
            setImageViewerIsOpened(false);
            setVideoIsOpen(true);
        } else {
            setVideoIsOpen(false);
            setImageViewerIsOpened(true);
        }
        const theseposts = isPool ? poolPosts : posts;
        const prevIndex = theseposts.indexOf(post)-1;
        const prev = prevIndex < 0 ? undefined : theseposts[prevIndex];
        const nextIndex = prevIndex+2;
        const next = nextIndex > theseposts.length ? undefined : theseposts[nextIndex];
        setPrevAndNextImage({prev: prev, next: next});
    }

    const followedTagsInPost = (post:Post) => {
        const includesAllComplexParts = (complextag:string, posttags:string[]):boolean => {
            for(let tagpart of complextag.toLowerCase().split(' ')){
                if(!posttags.includes(tagpart)){
                    return false;
                }
            }
            return true;
        }
        const tags = PostService.getTags();
        let foundtags:string[] = [];
        for(let tag of tags){
            if(tag.includes(' ') && includesAllComplexParts(tag, PostService.getAllTagsFromPost(post))){
                foundtags.push(tag);
            } else {
                if (PostService.getAllTagsFromPost(post).includes(tag)) {
                    foundtags.push(tag);
                }
            }
        }
        return foundtags;
    }

    const showPool = async (event:any, pool:number) => {
        event.preventDefault();
        setPoolPosts([]);
        setPoolOpened(true);
        const poolposts = await PostService.getPool(pool);
        setPoolPosts(poolposts);
    }

    const modalStyle:Modal.Styles = {
        content: {
            backgroundColor: 'black',
            top: screenWidth <= 720 ? '0%' : '3%',
            bottom: screenWidth <= 720 ? '0%' : '3%',
            left: screenWidth <= 720 ? '0%' : '3%',
            right: screenWidth <= 720 ? '0%' : '3%'
        }
    };

    const changePoolImageSize = (amount:number) => {
        let newAmount = poolImageSize + amount;
        if (newAmount > 0 && newAmount <= 100) {
            setPoolImageSize(newAmount);
        }
    }

    return (
        <>
            <div className='aside-grid-container'>
                <div>
                    <Link to="/e1547e/profile">Profile</Link>
                    <div style={{display: 'flex', justifyContent: 'space-around'}}>
                        <p><a href='#' onClick={() => changeImagesPerRow(-1)}>{'<<'}</a></p>
                        <p>{imagesPerRow}</p>
                        <p><a href='#' onClick={() => changeImagesPerRow(1)}>{'>>'}</a></p>
                    </div>
                    <div>
                        <input type='checkbox' id='show-artists' onClick={() => setShowAllArtists(!showAllArtists)} checked={showAllArtists} />
                        <label htmlFor='show-artists'>Always show info</label>
                    </div>
                    <addTags.AddTags refreshPosts={refreshPosts}/>
                    <TagList/>
                </div>
                <div style={{textAlign: 'center'}}>
                    <div style={{display: 'flex', flexWrap: 'wrap'}}>
                        {posts.map((post, index) => (
                            <div style={{width: `${100 / imagesPerRow}%`}} onMouseOver={() => setHoveredPost(post.id)} key={index}>
                                {post.file.ext === 'webm' && <h3>Video</h3>}
                                <img style={{width: '100%'}} src={post.sample.url} alt={String(post.id)} onClick={() => imageViewer(post, false)}/>
                                <div style={{display: hoveredPost === post.id || showAllArtists ? 'flex' : 'none', justifyContent: 'space-around'}}>
                                    <a className='highlight-link' href={post.file.url} target="_blank">original</a>
                                    <div>
                                        {post.tags.artist.map((artist, index) => (
                                            <p key={index} style={{margin: 0, padding: 0}}>
                                                {artist} <span className={PostService.getTags().includes(artist) ? 'artist-green' : 'artist-red'}>
                                                    {PostService.getTags().includes(artist) ? '' : ''}
                                                </span>
                                            </p>
                                        ))}
                                    </div>
                                    <a className='highlight-link' href={`https://e621.net/posts/${post.id}`} target="_blank">e621</a>
                                    {post.pools.length !== 0 && <div>
                                        {post.pools.map((pool, index) => (
                                            <p key={index} style={{margin: 0, padding: 0}}>
                                                <a href='#' onClick={event => showPool(event, pool)} className='highlight-link'>pool {index+1}</a>
                                            </p>
                                        ))}
                                    </div>}
                                    <a href='#' onClick={event => {event.preventDefault(); setExtendedPost(post.id)}}>{post.id === extendedPost ? '' : ''}</a>
                                </div>
                                {extendedPost === post.id &&
                                    <div>
                                        {followedTagsInPost(post).map((tag, index) => (
                                            <p style={{margin: 0, padding: 0}} key={index}>{tag}</p>
                                        ))}
                                    </div>}
                            </div>
                        ))}
                    </div>
                    <InfiniteScroll
                        hasMore={true}
                        loadMore={getPosts}
                    />
                    <h1><a href='#' onClick={getMore}>{loadMessage}</a></h1>
                </div>
            </div>
            {imageViewerIsOpened && <Lightbox
                mainSrc={viewedImage === undefined ? '' : viewedImage.file.url}
                mainSrcThumbnail={viewedImage?.sample.url}
                onCloseRequest={() => setImageViewerIsOpened(false)}
                nextSrc={prevAndNextImage.next?.file.url}
                nextSrcThumbnail={prevAndNextImage.next?.sample.url}
                prevSrc={prevAndNextImage.prev?.file.url}
                prevSrcThumbnail={prevAndNextImage.prev?.sample.url}
                onMoveNextRequest={() => {if (prevAndNextImage.next !== undefined) imageViewer(prevAndNextImage.next, isOpenedImageAPool)}}
                onMovePrevRequest={() => {if (prevAndNextImage.prev !== undefined) imageViewer(prevAndNextImage.prev, isOpenedImageAPool)}}
            />}
            <Modal isOpen={videoIsOpen} onRequestClose={() => setVideoIsOpen(false)} ariaHideApp={false} style={modalStyle}>
                <div>
                    <div style={{display: 'flex', flexWrap: 'wrap', justifyContent: 'space-between'}}>
                        <div className='modal-button'><Button onClick={() => {if (prevAndNextImage.prev !== undefined) imageViewer(prevAndNextImage.prev, false)}}>{'< Previous'}</Button></div>
                        <div className='modal-button'><Button onClick={() => setVideoIsOpen(false)}>Close video</Button></div>
                        <div className='modal-button'><Button onClick={() => {if (prevAndNextImage.next !== undefined) imageViewer(prevAndNextImage.next, false)}}>{'Next >'}</Button></div>
                    </div>
                    <ReactPlayer controls width="100%" height="calc(90vh - 90px)" url={viewedImage?.file.url} playing={true} loop={true}/>
                </div>
            </Modal>
            <Modal isOpen={isPoolOpened} onRequestClose={() => setPoolOpened(false)} ariaHideApp={false} style={modalStyle}>
                <div style={{display: 'flex', justifyContent: 'space-between'}}>
                    <div className='modal-button'>
                        <Button onClick={() => setPoolOpened(false)}>Close pool</Button>
                    </div>
                    <div style={{display: 'flex', justifyContent: 'space-around'}}>
                        <p><a href='#' onClick={(event) => {event.preventDefault(); changePoolImageSize(-5)}}>{'<<'}</a></p>
                        <p style={{padding: '0 1rem'}}>{poolImageSize}</p>
                        <p><a href='#' onClick={(event) => {event.preventDefault(); changePoolImageSize(5)}}>{'>>'}</a></p>
                    </div>
                </div>
                <div>
                    {poolPosts.map((post, index) => (
                        <img width={`${poolImageSize}%`} key={index} src={post.sample.url} alt={String(post.id)} onClick={() => imageViewer(post, true)} />
                    ))}
                </div>
                <div className='modal-button' style={{margin: '1rem 0 0 0', display: 'flex', justifyContent: 'center'}}>
                    <Button onClick={() => setPoolOpened(false)}>Close pool</Button>
                </div>
            </Modal>
        </>
    )
}

export default PostOverview